<template>
	<view class="main main-top-bg">
		<cu-custom class="head" :isBack="true">
			<block slot="backText">返回</block>
			<block slot="content">审批管理</block>
		</cu-custom>
		<scroll-view scroll-x class="nav">
			<view class="flex text-center">
				<view class="cu-item flex-sub" :class="index==TabCur?'text-orange cur':''" v-for="(item,index) in tabName" :key="index"
				 @tap="tabSelect" :data-id="index">
					{{item}}
				</view>
			</view>
		</scroll-view>
		<scroll-view scroll-y class="content" style="margin-top: 60rpx;background-color: #333;" v-if="TabCur == 0">
			<view class="flex  p-xs  mb-sm">
				<view class="flex-sub table-title-bg padding-sm">申请日期</view>
				<view class="flex-sub table-title-bg padding-sm">申请人</view>
				<view class="flex-sub table-title-bg padding-sm">类型</view>
				<view class="flex-sub table-title-bg padding-sm">办理</view>
			</view>
			<view class="flex  p-xs  mb-sm" v-for="(item,index) in applyList" :key="index">
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_DATE}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_PERSON_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.TYPE_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm" @click="approvalUrl(item)">办理</view>
			</view>
		</scroll-view>

		<scroll-view scroll-y class="content" style="margin-top: 60rpx;background-color: #333;" v-if="TabCur == 1">
			<view class="flex  p-xs  mb-sm">
				<view class="flex-sub table-title-bg padding-sm">申请日期</view>
				<view class="flex-sub table-title-bg padding-sm">申请人</view>
				<view class="flex-sub table-title-bg padding-sm">类型</view>
				<view class="flex-sub table-title-bg padding-sm">办理</view>
			</view>
			<view class="flex  p-xs  mb-sm" v-for="(item,index) in orderList" :key="index">
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_DATE}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_PERSON_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.TYPE_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm" @click="approvalUrl(item)">办理</view>
			</view>
		</scroll-view>


		<scroll-view scroll-y class="content" v-if="TabCur == 2">
			<view class="cu-bar solid-bottom margin-top text-white">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> {{checkTitle}}
				</view>
				<view class="action">
					<button class="cu-btn bg-blue shadow" @tap="showModal" data-target="gridModal">选择</button>
				</view>
			</view>
			<view class="flex  p-xs  mb-sm">
				<view class="flex-sub table-title-bg padding-sm">申请日期</view>
				<view class="flex-sub table-title-bg padding-sm">申请人</view>
				<view class="flex-sub table-title-bg padding-sm">仓库</view>
				<view class="flex-sub table-title-bg padding-sm">办理</view>
			</view>
			<view class="flex  p-xs  mb-sm" v-for="(item,index) in boundList" :key="index">
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_DATE}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_PERSION_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.OA_STOCK_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm" @click="approvalUrl(item)">办理</view>
			</view>
		</scroll-view>

		<scroll-view scroll-y class="content" v-if="TabCur == 3">
			<view class="cu-bar solid-bottom margin-top text-white">
				<view class="action">
					<text class="cuIcon-title text-orange "></text> {{checkTitle}}
				</view>
				<view class="action">
					<button class="cu-btn bg-blue shadow" @tap="showModal" data-target="gridModal">选择</button>
				</view>
			</view>
			<view class="flex  p-xs  mb-sm">
				<view class="flex-sub table-title-bg padding-sm">申请日期</view>
				<view class="flex-sub table-title-bg padding-sm">申请人</view>
				<view class="flex-sub table-title-bg padding-sm">仓库</view>
				<view class="flex-sub table-title-bg padding-sm">办理</view>
			</view>
			<view class="flex  p-xs  mb-sm" v-for="(item,index) in basisList" :key="index">
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_DATE}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.APPLY_PERSION_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm">{{item.OA_STOCK_NAME}}</view>
				<view class="flex-sub table-content-bg padding-sm" @click="approvalUrl(item)">办理</view>
			</view>
		</scroll-view>
		<view class="cu-modal" :class="modalName=='gridModal'?'show':''" @tap="hideModal">
			<view class="cu-dialog" @tap.stop>
				<radio-group class="block" @change="childChange">
					<view class="cu-list menu text-left">
						<view class="cu-item" v-for="(item,index) in checkList" :key="index">
							<label class="flex justify-between align-center flex-sub">
								<view class="flex-sub">{{item.MENU_NAME}}</view>
								<radio class="round" :value="(index) + ''" :class="childCol==index?'checked':''" :checked="childCol==index"></radio>
							</label>
						</view>
					</view>
				</radio-group>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		applyList,
		orderList,
		stockCheckMenu,
		stockList,
		constructionMenu,
		constructionList
	} from '../../api/user.js'
	export default {
		data() {
			return {
				TabCur: 0,
				scrollLeft: 0,
				approvalCur: 0,
				workCur: 0,
				goodsCur: 0,
				tabName: ['采购审批', '询价审批', '出库审批', '基建审批'],
				index: null,
				orgOptions: {},
				sumValue: 1000,
				applyList: [],
				orderList: [],
				boundList: [],
				basisList: [],
				checkList: [],
				childTab: 0,
				childScroll: 0,
				modalName: null,
				childCol: -1,
				checkTitle: '审批选择',
				checkState: -5
			}
		},
		onLoad(option) {
			this.getCurList('0')
			this.getCurList('1')
			// this.getCurList('2')
			// this.getCurList('3')
		},
		onShow() {
			this.applyList = []
			this.orderList = []
			this.boundList = []
			this.basisList = []
			this.getCurList('0')
			this.getCurList('1')
			this.getStockList(this.checkState)
		},
		methods: {
			getStockList(index) {
				var _this = this
				var data = {}
				_this.boundList = []
				_this.basisList = []
				data.STATUS = _this.checkState
				if (_this.TabCur == '2') {
					stockList(data).then(res => {
						if (res.list != '[]') {
							_this.boundList = res.list
						}
					})
				} else if(_this.TabCur == '3') {
					constructionList(data).then(res => {
						if (res.list != '[]') {
							_this.basisList = res.list
						}
					})
				}
			},
			childChange(e) {
				var _this = this
				_this.childCol = e.detail.value
				_this.checkTitle = _this.checkList[_this.childCol].MENU_NAME
				var state = _this.checkList[_this.childCol].url
				if (_this.TabCur == '2') {
					_this.checkState = state.substr(36)
				} else if (_this.TabCur == '3') {
					_this.checkState = state.substr(43)
				}
				_this.getStockList(_this.checkState)
				_this.hideModal()
			},
			tabSelect(e) {
				var _this = this
				_this.TabCur = e.currentTarget.dataset.id;
				_this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				_this.getCurList(_this.TabCur)
			},
			getCurList(index) {
				var _this = this
				index = index.toString()
				switch (index) {
					case '0':
						applyList().then(res => {
							_this.applyList = res.list
						})
						break;
					case '1':
						orderList().then(res => {
							_this.orderList = res.list
						})
						break;
					case '2':
						_this.checkTitle = '审批选择'
						_this.checkState = -5
						_this.childCol = -1
						stockCheckMenu().then(res => {
							_this.checkList = res
						})
						_this.boundList = []
						break;
					case '3':
						_this.checkTitle = '审批选择'
						_this.checkState = -5
						_this.childCol = -1
						constructionMenu().then(res => {
							_this.checkList = res
						})
						_this.basisList = []
						break;
				}
			},
			showModal(e) {
				this.modalName = e.currentTarget.dataset.target
			},
			hideModal(e) {
				this.modalName = null
			},
			approvalUrl(item) {
				var url = ''
				switch (this.TabCur.toString()) {
					case '0':
						url = 'approvalApply' + '?uid=' + item.PURCHASE_UUID + '&detp_id=' + item.DETP_ID + '&step=' + item.APPLY_STATUS_CODE +
							'&name=' + item.APPLY_STATUS_NAME + '&time=' + item.APPLY_DATE
						break;
					case '1':
						url = 'approvalOrder' + '?uid=' + item.PURCHASE_ORDER_UUID + '&detp_id=' + item.DETP_ID + '&step=' + item.ORDER_STATUS_CODE +
							'&name=' + item.ORDER_STATUS_CODE + '&time=' + item.APPLY_DATE + '&puid=' + item.PURCHASE_UUID
						break;
					case '2':
						url = 'approvalStock' + '?uid=' + item.ID
						break;
					case '3':
						url = 'approvalConstruction' + '?uid=' + item.ID
						break;
				}
				uni.navigateTo({
					url: url
				});
			}
		}
	}
</script>


<style>
	@import url("../../static/app.css");
	@import url("../../colorui/main.css");

	html,
	body {
		height: 100%;
		background-color: #333333;
	}

	uni-page {
		background-color: #333 !important;
	}

	uni-page-body {
		height: 100%;
		background-color: #333333;
	}

	uni-page-wrapper {
		background-color: #333;
	}

	.nav {
		color: white;
		height: 100rpx;
		margin-top: 50rpx;
	}

	.nav .cu-item.cur {
		background-color: #231e24;
		border-radius: 60rpx;
		border: 2px solid #352e35;
		box-shadow: 2px 3px 3px #000000;
		color: white;
	}

	.text-blue-my {
		color: #7c87da;
	}

	.tab-content {
		color: white;
	}

	.cu-list.menu>.cu-item {
		background-color: #333;
		color: #fff;
	}

	.cu-list.menu>.cu-item:after {
		border-bottom: 1px solid #7d7d7d;
	}
</style>
